@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$font;
    font-size: 1.31rem;
    color: v.$desaturatedDarkBlue;
    line-height: 2rem;
    letter-spacing: 0.12px;
    font-weight: 500;
    background-color: v.$lightGrayishBlue;
    height: 100vh;
    display: flex;
    align-items: center;
}

[class$="__container"] {
    padding: 0 2.4rem;
    max-width: 36.7rem;
    margin: 0 auto;

    @include m.tablet { 
        padding: 0;
        max-width: 73rem;
    }
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2 {
    font-weight: 700;
    color: v.$veryDarkGrayishBlue;
    margin: 0;
}

h1 {
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.2px;

    @include m.tablet { 
        font-size: 2rem;
        line-height: 2.8rem;
        letter-spacing: 0.5px;
    }
}

h2 {
    font-size: 1.3rem;
    line-height: 2rem;
    letter-spacing: 0.12px;
}

ul {
    padding: 0;
    list-style: none;
}

button {
    background-color: transparent;
    border: none;
}