@use 'base/variables' as v;
@use 'base/mixins' as m;

.article {

    &__content {
        border-radius: 1rem;
        position: relative;

        @include m.tablet { 
            display: grid;
            grid-template-columns: 28.5rem 44.5rem;
        }
    }

    &__image {
        width: 100%;
        height: 20rem;
        object-fit: cover;

        @include m.tablet { 
            height: 100%;
            object-position: left;
        }
    }

    &__info {
        background-color: v.$white;
        padding: 3.6rem 3.2rem 2rem;

        @include m.tablet { 
            padding: 3.2rem 4rem;
        }
    }

    &__text {
        margin: 1.2rem 0 3.2rem;

        @include m.tablet { 
            margin-bottom: 2rem;
        }
    }

    &__autor-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__autor {
        display: flex;
        align-items: center;
        gap: 1.6rem;
    }

    &__autor-image {
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
    }

    &__date {
        color: v.$grayishBlue;
    }

    &__share-btn {
        width: 3.2rem;
        height: 3.2rem;
        background-color: v.$lightGrayishBlue;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 1s;

        &:hover {
            cursor: pointer;
        }

        &.active {
            z-index: 1;
            transform: translateY(.8rem);
            background-color: v.$desaturatedDarkBlue;
            animation-name: shareBtn;
            animation-duration: 1s;

            svg path {
                fill: v.$white;
            }

            @include m.tablet { 
                transform: translateY(0);
                animation: none;
            }
        }
    }

}

.share {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 6.4rem;
    display: none;

    @include m.tablet { 
        left: initial;
        right: -6.8rem;
        bottom: 9.3rem;
    }

    &.active{
        display: block;
        animation-name: share;
        animation-duration: 1s;
    }

    &__contain {
        background-color: v.$veryDarkGrayishBlue;
        padding: 2.2rem 3.2rem;
        display: flex;
        align-items: center;
        position: relative;

        &::before {
            @include m.tablet { 
                content: '';
                width: 2.4rem;
                height: 2.4rem;
                background-color: v.$veryDarkGrayishBlue;
                position: absolute;
                bottom: -1.2rem;
                left: 50%;
                transform: translateX(-50%) rotate(45deg);
                box-shadow: 0px 30px 10px rgb(201 213 225 / 50%);
            }
        }
        
        @include m.tablet { 
            height: 5.5rem;
            width: 24.8rem;
            box-shadow: 0px 30px 10px rgb(201 213 225 / 50%);
            border-radius: 10px;
        }
    }

    &__text {
        color: v.$grayishBlue;
        letter-spacing: .5rem;
        margin-right: 2.1rem;
        text-transform: uppercase;
    }

    &__icon:not(:last-of-type) {
        margin-right: 1.6rem;
    }
}