html {
  font-size: 62.5%;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Manrope", sans-serif;
  font-size: 1.31rem;
  color: hsl(214deg, 17%, 51%);
  line-height: 2rem;
  letter-spacing: 0.12px;
  font-weight: 500;
  background-color: hsl(210deg, 46%, 95%);
  height: 100vh;
  display: flex;
  align-items: center;
}

[class$=__container] {
  padding: 0 2.4rem;
  max-width: 36.7rem;
  margin: 0 auto;
}
@media (min-width: 992px) {
  [class$=__container] {
    padding: 0;
    max-width: 73rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2 {
  font-weight: 700;
  color: hsl(217deg, 19%, 35%);
  margin: 0;
}

h1 {
  font-size: 1.6rem;
  line-height: 2.4rem;
  letter-spacing: 0.2px;
}
@media (min-width: 992px) {
  h1 {
    font-size: 2rem;
    line-height: 2.8rem;
    letter-spacing: 0.5px;
  }
}

h2 {
  font-size: 1.3rem;
  line-height: 2rem;
  letter-spacing: 0.12px;
}

ul {
  padding: 0;
  list-style: none;
}

button {
  background-color: transparent;
  border: none;
}

@keyframes share {
  0% {
    transform: translateX(-5rem);
    opacity: 0;
  }
  100% {
    transform: translateX(0rem);
    opacity: 1;
  }
}
@keyframes shareBtn {
  0% {
    transform: translateY(1rem);
    opacity: 0;
  }
  100% {
    transform: translateY(0.8rem);
    opacity: 1;
  }
}
.article__content {
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 992px) {
  .article__content {
    display: grid;
    grid-template-columns: 28.5rem 44.5rem;
  }
}
.article__image {
  width: 100%;
  height: 20rem;
  object-fit: cover;
}
@media (min-width: 992px) {
  .article__image {
    height: 100%;
    object-position: left;
  }
}
.article__info {
  background-color: #fff;
  padding: 3.6rem 3.2rem 2rem;
}
@media (min-width: 992px) {
  .article__info {
    padding: 3.2rem 4rem;
  }
}
.article__text {
  margin: 1.2rem 0 3.2rem;
}
@media (min-width: 992px) {
  .article__text {
    margin-bottom: 2rem;
  }
}
.article__autor-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.article__autor {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.article__autor-image {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
.article__date {
  color: hsl(212deg, 23%, 69%);
}
.article__share-btn {
  width: 3.2rem;
  height: 3.2rem;
  background-color: hsl(210deg, 46%, 95%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 1s;
}
.article__share-btn:hover {
  cursor: pointer;
}
.article__share-btn.active {
  z-index: 1;
  transform: translateY(0.8rem);
  background-color: hsl(214deg, 17%, 51%);
  animation-name: shareBtn;
  animation-duration: 1s;
}
.article__share-btn.active svg path {
  fill: #fff;
}
@media (min-width: 992px) {
  .article__share-btn.active {
    transform: translateY(0);
    animation: none;
  }
}

.share {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 6.4rem;
  display: none;
}
@media (min-width: 992px) {
  .share {
    left: initial;
    right: -6.8rem;
    bottom: 9.3rem;
  }
}
.share.active {
  display: block;
  animation-name: share;
  animation-duration: 1s;
}
.share__contain {
  background-color: hsl(217deg, 19%, 35%);
  padding: 2.2rem 3.2rem;
  display: flex;
  align-items: center;
  position: relative;
}
@media (min-width: 992px) {
  .share__contain::before {
    content: "";
    width: 2.4rem;
    height: 2.4rem;
    background-color: hsl(217deg, 19%, 35%);
    position: absolute;
    bottom: -1.2rem;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    box-shadow: 0px 30px 10px rgba(201, 213, 225, 0.5);
  }
}
@media (min-width: 992px) {
  .share__contain {
    height: 5.5rem;
    width: 24.8rem;
    box-shadow: 0px 30px 10px rgba(201, 213, 225, 0.5);
    border-radius: 10px;
  }
}
.share__text {
  color: hsl(212deg, 23%, 69%);
  letter-spacing: 0.5rem;
  margin-right: 2.1rem;
  text-transform: uppercase;
}
.share__icon:not(:last-of-type) {
  margin-right: 1.6rem;
}