

@keyframes share {
    0% {
        transform: translateX(-5rem);
        opacity: 0;
    }

    100% {
        transform: translateX(0rem);
        opacity: 1;
    }
}

@keyframes shareBtn {
    0% {
        transform: translateY(1rem);
        opacity: 0;
    }

    100% {
        transform: translateY(0.8rem);
        opacity: 1;
    }
}